.App {
  text-align: center;
}

/* sign up page */
.Signuppage{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:aliceblue;
  height: 100vh;
  width: 100vw;
  padding: 10px;
}
.sinuppageContainer{
  background-color:rgb(87, 168, 196 );
  width:80%;
  height:80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 10px 10px 5px lightblue;
}
#reg-head>h2{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.form-child{
  margin: 15px;
}

.form-child>input{
  width: 200px;
  height: 20px;
  border-radius: 0.5em;
}
.regis-btn{
  background-color: rgb(55, 168, 207 );
  color: white;
  height: 30px;
}
 

/* sign in page */
.SignIn{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:aliceblue;
  height: 100vh;
  width: 100vw;
  padding: 10px;
}
.SignIn-Container{
  background-color:rgb(87, 168, 196 );
  width:80%;
  height:80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 10px 10px 5px lightblue;
}


/* navbar  */

.Nav-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3%;
  background-color: rgb(191, 201, 202);
  
}
#nav-head{
  width: 100%;
  position: relative;
}
#nav-head>button:hover{
  cursor: pointer;
}
#nav-head>h1{
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-size: larger;
 /* text-decoration: underline solid 2px lightgoldenrodyellow; */

}
.searchInput-conatiner>input{
border-top-left-radius: 2em;
border-bottom-left-radius: 2em;
height: 20px;
/* border-right: 0; */
}
.searchInput-conatiner>button{
  border-top-right-radius: 2em;
  border-bottom-right-radius: 2em;
  height: 25px;
  background-color:rgb(62, 194, 26);
  color: white;
  border-left:0 ;
  /* font-size: medium; */
  font-style: italics;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}
.search-results{
  /* background-color: bisque; */
  margin: 2vh;
  padding: 10px;
  
}
.search-result-container{
  padding: 10px;
  background-color:rgb(23, 108, 136);
  box-shadow: 10px 10px 1px rgb(38, 146, 182);
}
.search-result-container>img{
  width: 40%;
  height: 40%;
}


/* tabs */
.tabs{
  background-color: rgb(191, 201, 202);
  display: flex;
  justify-content: center;
}
.tabs>div{
  border: 2px solid rgb(55, 168, 207 );
  margin: 15px;
  padding: 5px;
}

/* movie card  */
.list{
  background-color:black;
 display: flex;
 width: 100%;
 align-items: center;
justify-content: center;
flex-wrap: wrap;
}


.movie-card{
  border:2px solid white;
  margin:10px;
  padding: 15px;
  width:300px;
  height:450px;
  border-radius: 1em;
  color: white;
}
.mcc-left>img{
  width:80%;
  height:auto;
}
.card-footer{
  display: flex;
  justify-content: space-between;
}



/* tab div */
.tabs>div{
  background-color:rgb(96, 202, 50);
  color:white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
}
.tabs>div:hover{
background-color: yellow;
color: black;
cursor: pointer;
}





@media screen and (min-width:480px) {
  #nav-head>button{
    position: absolute;
    top: 0;
    right:0;
  }
   
}
@media screen and (max-width:480px) {
  .searchInputconatiner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
}
@media screen and (max-width:700px){
  .form-child>label{
    font-size:large;
    font-weight: 500;
    /* background-color: antiquewhite; */
  }
}